@tailwind base;
@tailwind components;
@tailwind utilities;
#Hs {
overflow-x: hidden;
}


.underlineHover::after {
  content: "";
  display: block;
  height: 3px;
  width: 100%;
  background-color: #819171; /* Set the underline color to orange */
  transform: scaleX(0); /* Initially, the underline will have no width (hidden) */
  transition: transform 0.2s ease; /* Add a transition for smooth effect */
}
.underlineHover:hover::after {
  transform: scaleX(1);
}